.header .container, .main .container {
    padding: 0 15px;
}

@media screen and (min-width: 576px) {
    .header .container, .main .container {
        padding: 0 35px;
    }
}

@media screen and (min-width: 768px) {
    .header .container, .main .container {
        padding: 0 50px;
    }
}

@media screen and (min-width: 1280px) {
    .header .container, .main .container {
        padding: 0;
        width: 1140px;
        margin: 0 auto;
    }
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-family: 'Montserrat', sans-serif;
    min-width: 320px;
}

.header {
    background-color: #fff;
    border: 1px solid;
}

@media (min-width: 768px) {
    .header {
        box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    }
}

.header__inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;
    height: 60px;
}

@media screen and (min-width: 768px) {
    .header__inner {
        height: 100px;
    }
}

.header__wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
}

.header__logo {
    margin-right: 45px;
}

@media screen and (min-width: 576px) {
    .header__logo {
        margin-right: 36px;
    }
}

@media screen and (min-width: 768px) {
    .header__logo {
        height: 23px;
    }
}

.header__city-link {
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    color: #5286EC;
}

@media screen and (min-width: 576px) {
    .header__city-link {
        display: none;
    }
}

.header__phone {
    display: none;
}

.header__phone span {
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    color: #B3B3B3;
}

@media screen and (min-width: 576px) {
    .header__phone {
        display: block;
    }
}

.header__phone-link {
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    color: #37373F;
}

@media screen and (min-width: 1280px) {
    .header__user {
        display: none;
    }
}

.main {
    color: #fff;
    background-image: url("./../images/main-bg.jpg");
    background-size: cover;
    background-position: 62% 0;
}

@media screen and (min-width: 768px) {
    .main {
        background-position: center;
    }
}

.main__inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    height: 655px;
}

@media screen and (min-width: 768px) {
    .main__inner {
        height: 840px;
    }
}

.main__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 39px;
    color: #fff;
    max-width: 247px;
    margin-top: -6px;
}

@media screen and (min-width: 768px) {
    .main__title {
        max-width: 596px;
        font-size: 40px;
        line-height: 49px;
    }
}

@media screen and (min-width: 1280px) {
    .main__title {
        max-width: 887px;
        width: 100%;
    }
}

.main__title span {
    color: #FC9B09;
}

.main__price-box {
    height: 93px;
    width: 208px;
    background-image: url(./../images/price-box.svg);
    background-repeat: no-repeat;
    margin: 36px 0 14px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    padding: 6px 15px;
    margin-left: -5px;
}

.main__wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}

.main__priceLabel {
    font-size: 18px;
    font-weight: 400;
    line-height: inherit;
    color: inherit;
}

.main__price {
    font-size: 30px;
    font-weight: 800;
    line-height: inherit;
    color: inherit;
}

.main__percent {
    border-radius: 50%;
    -webkit-filter: drop-shadow(0px 4px 15px rgba(255, 255, 255, 0.25)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    filter: drop-shadow(0px 4px 15px rgba(255, 255, 255, 0.25)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    max-height: 47px;
    margin-top: 10px;
    -webkit-transform: translateX(4px);
    transform: translateX(4px);
}

.main__btn {
    width: 275px;
    height: 68px;
    border: 3px solid #fff;
    border-radius: 7px;
    background-color: transparent;
    font-size: 16px;
    font-weight: 900;
    line-height: 20px;
    color: white;
    -webkit-filter: drop-shadow(0px 4px 70px rgba(255, 255, 255, 0.7)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    filter: drop-shadow(0px 4px 70px rgba(255, 255, 255, 0.7)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
}
